<template>
  <div id="loginWrapper">
    <div id="loginContent">
      <div class="loginContentItem">
        <h1>Login</h1>
        <form @submit.prevent>
          <transition name="fade">
            <div v-if="signupMode">
              <label for="inputName">Username:</label>
              <input
                type="text"
                v-model="inputName"
                name="inputName"
                placeholder="enter username"
              />
            </div>
          </transition>
          <label for="inputEmail">Email:</label>
          <input
            type="email"
            v-model="inputEmail"
            name="inputEmail"
            placeholder="enter email adress"
          />

          <label for="inputPassword">Password:</label>
          <input
            type="password"
            v-model="inputPassword"
            name="inputPassword"
            placeholder="***********"
          />
          <button
            @click="$router.push('/')"
          >{{ getButtonTxt }}</button>
          <p id="link" @click="signupMode = !signupMode">
            {{ getLinkTxt }}
          </p>
          <p id="cookieTxt">
            We use cookies and similar tools to improve your shopping
            experience, provide our services, understand how customers use our
            services so that we can make improvements, and to display
            advertising, including interest-based advertising.
          </p>
        </form>
      </div>
      <div class="loginContentItem"></div>
    </div>
  </div>

</template>

<script>
export default {
  emits: ["noNav"],
  data() {
    return {
      signupMode: false,
    };
  },
  computed: {
    getButtonTxt() {
      if (this.signupMode) {
        return "Create account";
      } else {
        return "Login";
      }
    },
    getLinkTxt() {
      if (this.signupMode) {
        return "Just login.";
      } else {
        return "No account? Sign up here!";
      }
    },
  },
  beforeMount() {
    this.$emit("noNav");
  },
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@800&display=swap");

#loginWrapper {
  background-color: white;
  height: 100vh;
  width: 100vw;
}

#loginContent {
  display: flex;
  flex-direction: row;
  height: 100%;
}

.loginContentItem {
  width: 50vw;
  height: 100%;
}

.loginContentItem:first-child {
  padding: 40px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h1 {
  font-family: "Open Sans", sans-serif;
  color: var(--blue);
  font-weight: 800;
  width: 26vw;
  font-size: 48px;
  margin: 0 0 60px 0;
}

label {
  font-family: "Raleway", sans-serif;
  color: black;
  font-weight: 600;
  font-size: 21px;
}

input {
  display: block;
  width: 25vw;
  font-family: "Raleway", sans-serif;
  color: white;
  margin: 15px 0 30px 0;
  padding: 15px 15px;
  border: none;
  border-radius: 8px;
  background-color: #6971c71e;
}

input::placeholder {
  color: black;
  opacity: 0.6;
}

button {
  margin: 10px 0 0 0;
  padding: 12px 30px;
  border: 2px solid var(--blue);
  background-color: var(--blue);
  font-family: "Raleway", sans-serif;
  border-radius: 8px;
  cursor: pointer;
  color: white;
  font-weight: 600;
  transition: all 0.2s ease-in-out;
  box-shadow: #6971c783 0px 8px 24px;
}

button:hover {
  background-color: white;
  color: var(--blue);
}

p {
  font-family: "Raleway", sans-serif;
  font-size: 12px;
  margin: 40px 0 0 0;
  cursor: pointer;
  color: var(--blue);
  text-decoration: underline;
}

#cookieTxt {
  max-width: 500px;
  text-decoration: none;
  color: black;
  font-size: 11px;
  line-height: 18px;
}

#backButton{
  position: absolute;
  top: 40px;
  left: 40px;
}

.loginContentItem:last-child {
  background-image: url("https://images.unsplash.com/photo-1552083940-86877723de7a?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2670&q=80");
  background-size: cover;
  background-position: center;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
  transform: scale(0.5);
}

.fade-enter-active {
  transition: all 0.2s ease-out;
}

.fade-leave-active {
  transition: all 0.2s ease-in;
}

.fade-enter-to,
.fade-leave-from {
  opacity: 1;
  transform: scale(1);
}
</style>