<template>
  <section>
    <div id="openItem" v-if="openItem">
      <div id="closeBg" @click="openItem = null"></div>
      <div id="openItemContent">
        <img
          id="closeItemButton"
          @click="openItem = null"
          src="../../assets/x.svg"
        />
        <div
          id="openItemImg"
          :style="{ backgroundImage: 'url( ' + openItem.imgLink + ' )' }"
        ></div>
        <h2>{{ openItem.title }}</h2>
        <p>{{ openItem.description }}</p>
        <button @click="addToCart(openItem)">Add to cart</button>
      </div>
    </div>
    <transition name="fadeOpacity">
      <div id="cartWrapper" v-if="cartVisibility">
        <div id="cartContent">
          <div id="cartNav">
            <h2>Your cart</h2>
            <img @click="closeCart" src="../../assets/x.svg" />
          </div>
          <div id="line"></div>
          <h3 v-if="cart.length == 0">
            It looks like your cart is empty, please add some items
          </h3>
          <div id="cartGrid">
            <div v-for="item in cart" :key="item.title" class="cartItem">
              <h3 class="cGridStart">{{ item.title }}</h3>
              <h3 class="cGridMid">{{ item.price }}</h3>
              <img
                class="cGridEnd"
                @click="spliceItem(item)"
                src="../../assets/trash.svg"
              />
            </div>
          </div>
          <div id="cartButtons">
            <div id="cartTotal">
              <h2>Total:</h2>
              <h2> {{ getCartTotal }}</h2>
            </div>
            <button
              class="buttonAlt"
              :class="{ inactive: cart.length == 0 }"
              @click="loadCreditcard"
            >
              Checkout
            </button>
          </div>
        </div>
      </div>
    </transition>
    <div id="wrapper">
      <div id="navbar">
        <div><h1>Webshop</h1></div>
        <div id="cartDiv" @click="openCart">
          <img @click="openCart" src="../../assets/shopping-cart.svg" />
          <transition name="fade">
            <div v-if="cart.length != 0" id="itemCounter">
              {{ cart.length }}
            </div>
          </transition>
        </div>
      </div>
      <div id="line"></div>
      <div id="content">
        <div class="listItem" v-for="item in shopItems" :key="item.title">
          <div
            class="itemImg"
            :style="{ backgroundImage: 'url( ' + item.imgLink + ' )' }"
            @click="openItem = item"
          ></div>
          <div class="itemInfo">
            <h2 @click="openItem = item">{{ item.title }}</h2>
            <p @click="openItem = item">{{ item.description }}</p>
            <button @click="addToCart(item)">Add to cart</button>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      shopItems: [
        {
          title: "Milano",
          description:
            "Soft Italian leather embraces you snugly. Stuffed with the finest materials, standing on black hardwood angeled feet. ",
          price: 299.99,
          imgLink:
            "https://images.unsplash.com/photo-1586023492125-27b2c045efd7?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1134&q=80",
        },
        {
          title: "Stockholm",
          description:
            "High quality cotton upholstery, lavishly stuffed with fine Australian wool. Finished in several stylish pastel colours.",
          imgLink:
            "https://images.unsplash.com/photo-1611967164521-abae8fba4668?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=750&q=80",
          price: 349.99,
        },
        {
          title: "Kiev",
          description:
            "Soupple and durable leather, made to support you body as best as possble. With a base of strong tender legs, supporting the structure.",
          imgLink:
            "https://images.unsplash.com/photo-1607541428479-41cb4f036642?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=716&q=80",
          price: 379.99,
        },
        {
          title: "Oslo",
          description:
            "Soupple and durable leather, made to support you body as best as possble. With a base of strong tender legs, supporting the structure.",
          imgLink:
            "https://unsplash.com/photos/sN4u56baSB0/download?ixid=MnwxMjA3fDB8MXxzZWFyY2h8MTB8fGRlc2lnbiUyMGNoYWlyfHwwfHx8fDE2MzkxMTk0ODA&force=true&w=1920",
          price: 379.99,
        },
        {
          title: "Berlin",
          description:
            "Soupple and durable leather, made to support you body as best as possble. With a base of strong tender legs, supporting the structure.",
          imgLink:
            "https://unsplash.com/photos/ssbgw3cKdXg/download?ixid=MnwxMjA3fDB8MXxzZWFyY2h8NXx8ZGVzaWduJTIwY2hhaXJ8fDB8fHx8MTYzOTExOTQ4MA&force=true&w=1920",
          price: 379.99,
        },
        {
          title: "Helsinki",
          description:
            "Soupple and durable leather, made to support you body as best as possble. With a base of strong tender legs, supporting the structure.",
          imgLink:
            "https://unsplash.com/photos/9489sFfgk4c/download?ixid=MnwxMjA3fDB8MXxhbGx8fHx8fHx8fHwxNjM5MTQxODkw&force=true&w=1920",
          price: 379.99,
        },
      ],
      cart: [],
      cartVisibility: false,
      openItem: null,
    };
  },
  methods: {
    addToCart(item) {
      item.id = this.cart.length;
      this.cart.push(item);
      this.openItem = null;
    },
    openCart() {
      this.cartVisibility = true;
    },
    closeCart() {
      this.cartVisibility = false;
    },
    spliceItem(item) {
      const cart = this.cart;
      for (let i = 0; i < cart.length; i++) {
        if (this.cart[i].title == item.title) {
          this.cart.splice(i, 1);
        }
      }
    },
    loadCreditcard() {
      if (this.cart.length != 0) {
        this.$router.push("/creditcard/UI/Creditcard/");
      }
    },
  },
  computed: {
    getCartTotal() {
      var total = 0;
      const cart = this.cart;
      for (let i = 0; i < cart.length; i++) {
        total = total + cart[i].price;
      }
      return total;
    },
  },
};
</script>

