<template>
  <section>
    <div id="graphWrapper">
      <div id="chart">
        <apexchart
          type="area"
          id="graph1"
          :options="chartOptions"
          :series="series"
          ref="chart1"
        ></apexchart>
      </div>
    </div>
    <div id="controlWrapper">
      <div class="controlItem">
        <button
          class="nl"
          @click="hide('Nederland')"
          :class="{ activeNL: showSeries.Nederland }"
        >
          Nederland
        </button>
        <button
          class="de"
          @click="hide('Duitsland')"
          :class="{ activeDE: showSeries.Duitsland }"
        >
          Duitsland
        </button>
      </div>
      <div class="controlItem">
        <button @click="resetZoom">
          <fa class="ico" icon="search-minus"></fa>Reset zoom
        </button>
      </div>
    </div>
  </section>
</template>

<script>
import VueApexCharts from "vue3-apexcharts";

export default {
  components: {
    apexchart: VueApexCharts,
  },
  data() {
    return {
      showSeries: {
        Nederland: true,
        Duitsland: true,
      },
      series: [
        {
          name: "Nederland",
          data: [
            1042, 879, 1040, 1024, 1061, 894, 693, 713, 570, 700, 666, 697, 648,
            556, 502, 568, 530, 643, 825, 942, 1126, 1205, 1519, 2198, 3616,
            5405, 6897, 10260, 9329, 8472, 7828, 10434, 11018, 11324, 11134,
          ],
        },
        {
          name: "Duitsland",
          data: [
            1000, 873, 1160, 1439, 901, 933, 996, 529, 493, 685, 1135, 475, 751,
            488, 336, 341, 569, 806, 754, 671, 429, 441, 392, 713, 992, 903,
            885, 809, 568, 553, 1027, 1623, 1444, 1565, 1309,
          ],
        },
      ],
      chartOptions: {
        chart: {
          type: "area",
          toolbar: {
            show: false,
          },
        },

        theme: {
          palette: "palette1",
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          curve: "smooth",
        },
        legend: {
          show: false,
          fontSize: "14px",
          fontWeight: "600",
          labels: {
            colors: "white",
            useSeriesColors: false,
          },
          markers: {
            width: 18,
            height: 18,
            strokeWidth: 0,
            strokeColor: "#fff",
            fillColors: undefined,
            radius: 5,
            customHTML: undefined,
            onClick: undefined,
            offsetX: 0,
            offsetY: 0,
          },
          itemMargin: {
            horizontal: 15,
            vertical: 0,
          },
        },
        xaxis: {
          type: "datetime",
          categories: [
            "13 jun",
            "14 jun",
            "15 jun",
            "16 jun",
            "17 jun",
            "18 jun",
            "19 jun",
            "20 jun",
            "21 jun",
            "22 jun",
            "23 jun",
            "24 jun",
            "25 jun",
            "26 jun",
            "27 jun",
            "28 jun",
            "29 jun",
            "30 jun",
            "1 jul",
            "2 jul",
            "3 jul",
            "4 jul",
            "5 jul",
            "6 jul",
            "7 jul",
            "8 jul",
            "9 jul",
            "10 jul",
            "11 jul",
            "12 jul",
            "13 jul",
            "14 jul",
            "15 jul",
            "16 jul",
            "17 jul",
          ],
        },
        tooltip: {
          x: {
            format: "dd/MM/yy HH:mm",
          },
        },
      },
    };
  },
  methods: {
    resetZoom() {
      this.$refs.chart1.zoomX();
    },
    hide(id) {
      this.showSeries[id] = !this.showSeries[id];
      if (this.showSeries[id] == true) {
        this.$refs.chart1.showSeries(id);
      }
      if (this.showSeries[id] == false) {
        this.$refs.chart1.hideSeries(id);
      }
    },
  },
};
</script>

<style scoped>
section {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  justify-content: center;
  align-items: center;
}

#graphWrapper {
  padding: 40px;
  background-color: #222;
  border-radius: 20px;
}

#controlWrapper {
  display: flex;
  justify-content: space-between;
  width: 1080px;
}

.controlItem {
  padding: 40px;
  background-color: #222;
  border-radius: 20px;
}

#graph1 {
  width: 1000px;
}

button {
  border: solid 1px #00e396;
  padding: 12px 18px;
  background-color: #00e396;
  margin: 0 15px;
  border-radius: 5px;
  color: white;
  font-family: "raleway", sans-serif;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.2s ease-in-out;
}

.ico {
  margin: 0 10px 0 0;
}

.de,
.nl {
  background-color: transparent;
}

.de {
  border-color: #00e396;
  color: #00e396;
}

.nl {
  border-color: #008ffb;
  color: #008ffb;
}

.activeDE {
  background-color: #00e396;
  color: white;
}

.activeNL {
  background-color: #008ffb;
  color: white;
}

button:hover {
  transform: scale(1.05);
}
</style>