              :class="{ slideActive: !monthly }"
              @click="monthly = false"
            >
              <p>Annually</p>
            </div>
          </div>
        </div>
        <div id="cardsWrapper">
          <div class="card" :class="{ selectedCard: selected == 0 }">
            <div class="price">
              <h3>$</h3>
              <h2>0</h2>
            </div>
            <transition name="fade" mode="out-in">
              <p v-if="monthly">/ Month</p>
              <p v-else>/ Year</p>
            </transition>
            <ul>
              <li>Our Client Suite</li>
              <li>5 clients</li>
              <li>Customer support</li>
              <li class="grey">No brand identity</li>
              <li class="grey">128 bit encryption</li>
            </ul>
            <button
              :class="{ activeButton: selected == 0 }"
              @click="selected = 0"
            >
              {{ getButtonTxt(0) }}
            </button>
          </div>
          <div
            class="card"
            id="featuredCard"
            :class="{ selectedCard: selected == 1 }"
          >
            <div id="featuredDiv">POPULAR</div>
            <div class="price">
              <h3>$</h3>
              <transition name="fade" mode="out-in">
                <h2 v-if="monthly">6</h2>
                <h2 v-else>60</h2>
              </transition>
            </div>
            <transition name="fade" mode="out-in">
              <p v-if="monthly">/ Month</p>
              <p v-else>/ Year</p>
            </transition>
            <ul>
              <li>Our Client Suite</li>
              <li>25 clients</li>
              <li>24/7 Customer support</li>
              <li>Custom brand identity</li>
              <li>256 bit encryption</li>
            </ul>
            <button
              id="featuredButton"
              :class="{ activeButton: selected == 1 }"
              @click="selected = 1"
            >
              {{ getButtonTxt(1) }}
            </button>
          </div>
          <div class="card" :class="{ selectedCard: selected == 2 }">
            <div class="price">
              <h3>$</h3>
              <transition name="fade" mode="out-in">
                <h2 v-if="monthly">12</h2>
                <h2 v-else>125</h2>
              </transition>
            </div>
            <transition name="fade" mode="out-in">
