              <li>100 clients</li>
              <li>24/7 Customer support</li>
              <li>Multiple brand identities</li>
              <li>256 bit encryption</li>
            </ul>
            <button
              :class="{ activeButton: selected == 2 }"
              @click="selected = 2"
            >
              {{ getButtonTxt(2) }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      selected: 1,
      monthly: true,
    };
  },
  methods: {
    getButtonTxt(num) {
      if (num == this.selected) {
        return "Selected";
      } else {
        return "Get started!";
      }
    },
    getPrice(price) {
      if (this.monthly) {
        return price;
      } else if (price == 6) {
        return 60;
      } else {
        return 125;
      }
    },
  },
  computed: {},
};
</script>

