import { createRouter, createWebHistory } from "vue-router";
import TheCreditCard from "../components/views/TheCreditCard.vue";
import TheHomeView from "../components/views/TheHomeView.vue";
import ThePricing from "../components/views/ThePricing.vue"
import TheWebshop from "../components/views/TheWebshop.vue"
import TheDatavis from "../components/views/TheDatavis.vue"
import TheLoginForm from "../components/views/TheLoginForm.vue"
import TheItemView from "../components/views/TheItemView.vue"
import ThePricing2 from "../components/views/ThePricing2.vue"

const routes = [
  { path: "/", component: TheHomeView },
  {
    path: "/creditcard/:category/:name",
    name: "Creditcard",
    title: "| Creditcard component",
    component: TheCreditCard,
    metaTags: [
      {
        name: "| Creditcard component",
      },
      {
        property: "og:| Creditcard component",
      },
    ],
  },
  {path: "/pricing/:category/:name", component: ThePricing},
  {path: "/webshop/:category/:name", component: TheWebshop},
  {path: "/datavis/:category/:name", component: TheDatavis},
  {path: "/login/:category/:name", component: TheLoginForm},
  {path: "/item/:category/:name", component: TheItemView},
  {path: "/pricing2/:category/:name", component: ThePricing2},

];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
